header {
    display: flex;
    justify-content: space-between;
    padding: 44px 60px;
    border-bottom: 1px solid #EAEAEA;

    a{
        text-decoration: none;
    }
  
    .headerLeft {
        display: flex;
        align-content: center;
        justify-content: space-around;
  
        img {
            margin-right: 16px;
        }
  
        p {
            color: #9D9D9D;
            font-size: 14px;
            font-weight: 400;
            line-height: 16.94px;
            text-align: left;
            margin: 0px;
        }
  
        span {
            color: black;
            font-size: 20px;
            font-weight: 700;
            line-height: 24.2px;
            text-align: left;
            text-transform: uppercase;
        }
  
        div {
            display: flex;
            flex-direction: column;
        }
    }
  
    nav {
        ul {
            display: flex;
            align-content: center;
            justify-content: space-around;
            align-items: center;
        }
  
        li {
            margin-left: 32px;
        }
  
        a {
            text-decoration: none;
            display: flex;
            align-items: center;
            cursor: pointer;
        }
  
        .cart span {
            font-weight: 600;
            margin: 0;
        }
  
        span {
            font-size: 14px;
            font-weight: 400;
            line-height: 16.94px;
            text-align: left;
            color: #5C5C5C;
            padding-left: 8px;
        }
    }
  }
  